import uCharts from '@/components/u-charts/u-charts.js';
import { isJSON } from '@/common/checker.js';
var _self;
var canvaColumn = null;
export default {
	data() {
		return {
			// 图表
			touchColumn_data: [], // 只显示一个
			cavas_Br: false,
			ColumnStack_x: [], // 图标的日期
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			textarea: '',
			// 图表
			xs_Br_tow: true,
			e: {},
			xs_Br: true,
			Alldata: {
				bdate: '2019-09-06',
				brd: null,
				city: null,
				content:
					'《最长1枪》在2019-09-04上映前第2天，再次与北京唐阁沟通，排片增加13场，此次排场占该影投排场量的3.79%，到目前为止，北京唐阁旗下共有19家影院为《最长1枪》开设了预售场次，累计排场达14场，占比该影投当日排场4.08%',
				district: null,
				image: '/uploads/movie_thumb/0/0_300X500_6650_89d81629.jpg',
				img: Object,
				movie_id: '6650',
				new_type: '3',
				nid: '096a8543857f78228d514521c5639151',
				read_data: '4',
				sdate: '2019-09-04',
				title: '影片《最长1枪》在北京唐阁影投大规模排片',
				updated: '2019-09-04',
				zan_data: null
			},
			id: ''
		};
	},
	onLoad(res) {
		_self = this;
		this.e = res;
		let id = res.id;
		let brd = res.brd;
		let district = res.district;
		let city = res.city;
		var url = 'https://db.topcdb.com/topprogram/get-report-detail?id=' + id + '&city=' + city + '&district=' + district + '&brd=' + brd;
		// console.log(url);
		this.id = id;
		this.db_index(url);

		var url = 'https://db.topcdb.com/topprogram/add-read-data?id=' + id;
		uni.request({
			url
		});












		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
	},
	methods: {
		ys_fb(index) {
			var { movie_id: id, brd, district, city,id:mid } = this.Alldata;
			if (brd == null) {
				brd = '';
			}
			if (district == null) {
				district = '';
			}
			if (city == null) {
				city = '';
			}
			
			if (index == 0) {
				uni.navigateTo({
					url:'./budget_data/budget_data?brd=' + brd + '&district=' + district + '&city=' + city + '&num=' + 0
				})
			} else if (index == 1) {
				
				uni.navigateTo({
					url:'./budget_data/budget_data?movie_id=' + id + '&brd=' + brd + '&district=' + district + '&city=' + city + '&num=' + 1
				})
			}
		},
		// 跳转当前影片的 详细
		tz_yp_index(index) {
			if (index == 1) {
				var { movie_id: movie_id } = this.Alldata;
				uni.navigateTo({
					url: './re_list/re_list?movie_id=' + movie_id + '&Br=' + true
				});
			} else {
				var res = this.e;
				let brd = res.brd;
				let district = res.district;
				let city = res.city;

				uni.navigateTo({
					url: './re_list/re_list?brd=' + brd + '&district=' + district + '&city=' + city
				});
				this.xs_Br = true;
			}
		},
		// 图表

		showColumnStack(canvasId, chartData) {
			canvaColumn = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'column',
				legend: true,
				fontSize: 11,
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				animation: true,
				categories: chartData.categories,
				series: chartData.series,
				xAxis: {
					disableGrid: true
				},
				yAxis: {
					// disabled:true
				},
				dataLabel: false,
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					column: {
						type: 'stack',
						width: (_self.cWidth * _self.pixelRatio * 0.5) / chartData.categories.length
					}
				}
			});
		},
		touchColumn(e) {
			var x = [...this.ColumnStack_x];
			var ins = canvaColumn.getCurrentDataIndex(e);
			// console.log(ins);
			var that = this;
			canvaColumn.showToolTip(e, {
				format: function(item, category) {
					// category
					return x[ins] + ' ' + item.name + ':' + item.data;
				}
			});
		},
		// 图表
		showloading(index) {
			if (index == 1) {
				var res = this.Alldata;
				var id = res.id;
				var zz_id = res.zz_id;
				uni.navigateTo({
					url: '../../../pagesA/dy_particulars/dy_particulars?mid=' + id + '&id=' + zz_id
				});
			} else {
				uni.showToast({
					title: '暂无内容，敬请期待',
					duration: 2000,
					icon: 'none'
				});
			}
		},
		// 跳转 注册页
		web_register_data() {
			uni.redirectTo({
				url: './zc_tz/zc_tz'
			});
		},

		// name跳转
		header_center_name() {
			var Br = this.xs_Br_tow;
			if (Br) {
				Br = false;
			} else {
				Br = true;
			}
			this.xs_Br_tow = Br;
		},
		xs_tap() {
			var Br = this.xs_Br;
			if (Br) {
				Br = false;
			} else {
				Br = true;
			}
			this.xs_Br = Br;
		},
		db_index(url) {
			uni.request({
				url
			}).then(dd => {
				let [errer, ra] = dd;
				var res = ra.data;
				console.log(res);
				res.updated = res.updated.slice(0, 10);
				for (let i in res.img) {
					res.image = res.img[i];
				}
				var a = res.content.split('。');
				if (res.read_data == null) {
					res.read_data = 0;
				}
				// console.log(res.brd);
				if (res.brd != null) {
					res.bd_name = res.brd;
				} else if (res.city != null) {
					res.bd_name = res.city;
				} else if (res.district != null) {
					res.bd_name = res.district;
				}

				// 图表
				let ColumnStack = { categories: [], series: [] };
				var name = '';
				var index = 0;
				var Tb_Br = false;
				var color = ['#FF8800', '#4169E1'];
				for (let i in res.js) {
					if (res.js[i].length != 0 && i != 'x') {
						// console.log(i);
						if ('session' == i) {
							name = '昨日场次';
						} else if ('xz_session' == i) {
							name = '新增场次';
						} else if ('gd_session' == i) {
							name = '黄金场';
						} else if ('yl_session' == i) {
							name = '幽灵场';
						} else if ('total_bo' == i) {
							name = '累计票房';
						} else if ('xz_bo' == i) {
							name = '新增票房';
						}

						if (res.js.session === undefined) {
							Tb_Br = true;
							_self.cavas_Br = Tb_Br;
						} else {
							var arr = [];
							arr = res.js[i].map((item, i) => {
								return Number(item);
							});
							ColumnStack.series.push({ name: name, data: arr, color: color[index] });
							index++;
						}
					}
				}

				if (Tb_Br) {
				} else {
					var x = res.js.x;
					var ar_X = [];
					if (x.length > 3) {
						ar_X = x.map(item => {
							return item.slice(5);
						});
						ColumnStack.categories = ar_X;
					} else {
						ColumnStack.categories = res.js.x;
					}
					// console.log(ColumnStack);
					_self.showColumnStack('canvasColumnStack', ColumnStack);
					// 图表
					// console.log(res);
					this.cavas_Br = Tb_Br;
					this.ColumnStack_x = x;
				}

				this.Alldata = res;
			});
		}
	}
};
